.intro{
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}
.i-left{
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;
}

.i-name{
    display: flex;
    flex-direction: column;
}

.i-name>:nth-child(1){
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
}

.i-name>:nth-child(2){
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}

.i-name>:nth-child(3){
    color: var(--gray);
    font-size: 14px;
    font-weight: 100;
}

.i-button{
    width: 6rem;
    height: 2rem;

}

.i-icons{
    margin-top: 2rem;
    display: flex;
    gap: 1rem;
}

.i-icons>*{
    transform: scale(.5);
    transition: all .3s linear;
}

.i-icons>*:hover{
    cursor: pointer;
    transform: scale(.7);
}

.i-right{
    flex: 1;
    position: relative;
}
.i-right>*{
    position: absolute;
    z-index: 1;
}

.i-right>:nth-child(1){
    transform: scale(.69);
    left: -15%;
    top: -9%;
}

.i-right>:nth-child(2){
    transform: scale(.67);
    left: -3rem;
    top: -4.6%;
}

.i-right>:nth-child(3){
    transform: scale(.67);
    top: -20.6%;
    left: -20rem;
}

.i-right>:nth-child(4){
    transform: scale(1.4);
    left: 20%;
    top: 4.6%;
}

.i-right>:nth-child(5){
    transform: scale(.6);
    left: -4rem;
    top: -2rem;
}

/* blur div */
.blur{
    height: 14rem;
    width: 22rem;
    position: absolute;
    border-radius: 50%;
    background: #edd0ff;
    z-index: -9;
    top: -18%;
    left: 56%;
    filter: blur(72px);
}

