.contact-form{
    padding: 0 3rem;
    display: flex;
    height: 80vh;
    margin-top: 2rem;
}

.c-right{
    display: flex;
    justify-content: center;
    position: relative;
    flex: 1;
}

.c-right>form{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
}

.c-right .user{
    width: 20rem;
    height: 2rem;
    padding: .3rem;
    outline: none;
    border: 3px solid var(--orange);
    border-radius: 8px;
    font-size: 16px;
}

textarea{
    height: 4rem !important;
}

.c-blur1{
    top: 1rem;
    left: 8rem;
}