.floatingdiv{
    justify-content: space-around;
    display: flex;
    background: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center;
    padding: 0px 28px 0px 0px ;
    height: 4.5rem;
}

.floatingdiv:hover>img{
    transform: scale(.6);
    transition: all .4s linear;
}

.floatingdiv>img{
    transform: scale(.4);
}

.floatingdiv>span{
    font-family: sans-serif;
    font-size: 16px;
}